
$input_focus:#64dd17;

.input-field input:focus+label,select:focus+label {
    color: $input_focus!important;
}
.input-field input:focus {
    border-bottom: 1px solid $input_focus!important;
    box-shadow: 0 1px 0 0 $input_focus!important;
    color: $input_focus;

    select option{
        color: $input_focus;
    }
    
}
form{
    margin-top: 3rem;
}
.dropdown-content>li>span{
    color: $input_focus;
}
