

header{
    padding: 5px 0;
    h1{
        margin: 0;
        margin-left: 10rem;
        font-size: 2.5rem;
        font-weight: 200;
    }

    @media screen and (min-width:700px){
        h1{
            font-size: 3rem;
        }
    }
    
    .message{
        position: fixed;
        z-index: 2;
        top: 5rem;
        right: 2rem;
        background:yellow;
        padding: 1rem;
        font-size: 3rem;
        color: white;
        animation: anim 100ms infinite linear;
        span{
            filter: drop-shadow(0 0 3px black);
        }
        @keyframes anim {
            0%{
                filter: hue-rotate(0deg);
            }
            100%{
                filter: hue-rotate(360deg);
            }
        }
    }


}


