
.chart{
    height: 60vh;
    margin: auto 0;
}

.results{
    padding: 1rem;
    border-radius: 5px;
    color: #474646;
    margin-top: 2rem!important;
    p{
        margin: .5rem 0;
        font-weight: 400;
        font-size: 1.3rem;
    }
    span{
        color: rgb(126, 161, 90);
        font-size: 1.5rem;
        margin-left: 5px;
        &::after{
            content: '$';
        }
        font-size: 1.3rem;
    }

}